<template>
  <v-main class="pa-0 mt-6">
    <v-row>
      <v-col cols="12" md="9">
        <v-alert
            border="left"
            elevation="1"
            type="info"
        >
          Google Tag Manager functions best when deployed alongside a data layer.
          A data layer is a JavaScript object that is used to pass information from your website to your
          Tag Manager container. You can then use that information to populate variables and activate
          triggers in your tag configurations.
        </v-alert>

        <v-data-table
            :headers="headers"
            :items="items"
            item-key="variable"
            sort-by="variable"
            group-by="view"
            class="elevation-1"
            show-group-by
        >
          <template
              v-slot:item.variable="{ item }"
          >
            <code class="purple--text">{{ item.variable }}</code>
          </template>
        </v-data-table>
      </v-col>
      <v-col cols="12" md="3">
        <div class="text-center" style="margin-bottom: -13px;">
          <v-icon
              large
              color="success"
          >
            mdi-heart
          </v-icon>
        </div>

        <v-sheet
            color="success"
            rounded
            class="pa-3 mb-6"
        >
          <p class="text-center text--secondary ma-0">
            Want to hire me to implement Google Tag Manager, including full dataLayer, in your site/store?
            I'm certified in <strong>Google Tag Manager</strong> and <strong>Google Analytics</strong> by Google
            and have more than 13 years of experience in PHP/HTML and have developed a lot of plugins for
            <strong>WooCommerce</strong> and <strong>CS-Cart.</strong><br>
            Please consider giving a small donation including your email and I will get in contact you ASAP.<br>
            Remember, you will be helping many stray animals in Cuba.<br>
            <v-icon small>
              mdi-cat
            </v-icon>
            <v-icon small>
              mdi-heart
            </v-icon>
            <v-icon small>
              mdi-dog
            </v-icon>
          </p>
        </v-sheet>

        <div class="text-center mb-4">
          <Donate></Donate>
        </div>

        <v-divider class="mb-6 success"></v-divider>
      </v-col>
    </v-row>
  </v-main>
</template>

<script>
import Donate from "@/components/Donate.vue";

export default {
  name: "Datalayer",
  title: 'dataLayer Reference',
  components: {Donate},
  data() {
    return {
      headers: [
        {
          text: "Variable",
          align: "start",
          value: "variable",
          groupable: false,
        },
        {
          text: "Type",
          align: "start",
          value: "type",
          groupable: false,
        },
        {
          text: "Description",
          align: "start",
          value: "description",
          groupable: false,
        },
        {
          text: "View",
          align: "start",
          value: "view",
        },
      ],
      items: [
        {
          variable: "userId",
          type: "Number",
          description: "The ID of the logged user in your store, 0 for anonymous.",
          view: "All Pages",
        },
      ],
    };
  },
}
</script>

